import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/home', component: Home },
  {
    path: '/home',
    name: '/home',
    component: () => import('@/views/Home')
  },
  {
    path: '/detail/:id',
    name: 'Detail',
    component: () => import('@/views/detail'),
    props: true
  }
]

const router = new VueRouter({
  routes
})

export default router
